import React, {useState} from "react";
import {Box} from "@mui/material";
import {HeroData} from "../types";
import {HeroModernIcon} from "./HeroModernIcon";

interface IProps {
    heroProb?: Map<number,number>,
    onClick: any,
    selectedHeroes: number[],
    highlightedHeroes: number[],
    addedHeroes?: number[],
    removedHeroes?: number[],
    additionalClasses: string[]
}

interface IState {

}

export function HeroModernGrid(props:IProps) {// extends React.Component<IProps, IState> {
    const [statFilterEnabledS,setStatFilterEnabledS] = useState(true)
    const [statFilterEnabledA,setStatFilterEnabledA] = useState(true)
    const [statFilterEnabledI,setStatFilterEnabledI] = useState(true)

    const renderHeroIcons = function () {
        if (props.addedHeroes) {console.log('renderHeroIcons',props.addedHeroes,props.removedHeroes)}
        const items=[]

        for (const i in HeroData.get_instance().heroIdsOrderedByName) {
            const heroId=HeroData.get_instance().heroIdsOrderedByName[i]
            const prob=props.heroProb?.get(heroId) || 0
            const heroItem=HeroData.getHeroById(heroId)
            const additionalClasses=["heroIconSmall"]
            if (props.selectedHeroes.indexOf(heroId)>=0) {additionalClasses.push("mySelected")}
            if (props.highlightedHeroes.indexOf(heroId)<0) {additionalClasses.push("myNotHighlighted")}
            if (!heroItem?.mainStat || (statFilterEnabledS && heroItem?.mainStat==="strength") || (statFilterEnabledA && heroItem?.mainStat==="agility") || (statFilterEnabledI && heroItem?.mainStat==="intelligence")) {
                items.push(
                    <HeroModernIcon key={i} additionalClasses={additionalClasses} heroId={heroId} heroProb={prob}
                                    onClick={props.onClick} isAdded={props.addedHeroes?.includes(heroId)}
                                    isRemoved={props.removedHeroes?.includes(heroId)}/>
                )
            }
        }
        return items
    }
    const toggleS=function() {
        setStatFilterEnabledS(!statFilterEnabledS)
    }
    const toggleA=function() {
        setStatFilterEnabledA(!statFilterEnabledA)
    }
    const toggleI=function() {
        setStatFilterEnabledI(!statFilterEnabledI)
    }

    //render() {

        return (
            <>
                <Box sx={{display:'flex',flexDirection: 'row',alignItems: 'center', '& > img': {
                        height: 30,width: 30, margin: '5px', border: '2px solid', borderRadius: '50%',background: 'grey'
                    }}}>
                    <img src={"./strength.png"} style={{opacity: statFilterEnabledS?1:0.5,borderColor: 'red'}} onClick={toggleS} alt={"strength"}/>
                    <img src={"./agility.png"} style={{opacity: statFilterEnabledA?1:0.5,borderColor: 'blue'}} onClick={toggleA} alt={"agility"}/>
                    <img src={"./intelligence.png"} style={{opacity: statFilterEnabledI?1:0.5,borderColor: 'green'}} onClick={toggleI} alt={"intelligence"}/>
                </Box>
            <Box className={"heroModernGrid " + props.additionalClasses.join(" ")}>
                {renderHeroIcons()}
            </Box>
            </>
        )
  //  }
}